export default class SelectedValues {
    constructor () {
        this.pointsSpan = document.getElementById('selected-mark')
        this.nodes = [
            document.getElementById('lower-value'),
            document.getElementById('upper-value')
        ]
        this.popupBtn = document.querySelector('[data-js-popup-btn]')
        this.rangeSlider = document.querySelector('#range');
        this.init()
    }

    init() {
        this.rangeSlider.noUiSlider.on('update', (values, handle) => {
            this.nodes[handle].innerHTML = Number(values[handle]).toFixed() + ' $';
        });

        if (this.popupBtn) {
            this.popupBtn.addEventListener('click', () => {
                const activeMark = document.querySelector('.map__mark--active')
                this.pointsSpan.innerHTML = activeMark ? activeMark.dataset.markCoord.split(",").join(', ') : '0, 0'
            })
        }
    }
}