export default class Maps {
    coordsElem = '[data-map-coords]';
    // mapElem = '[data-map-script]'
    constructor() {
        this.events()
        this.mapCoords = [];
        this.mapCenter = [];
        this.ymaps = window.ymaps;
    }

    events() {
        $(window).on('load', () => {
            this.handleLoad();
        });
    }

    handleLoad() {
        const mapContainer = $(this.coordsElem)
        // const mapScript = $(this.mapElem)
        if ($(mapContainer).length) {
            // mapScript[0].src = 'https://api-maps.yandex.ru/2.1/?apikey=6dbfce26-c116-4990-9f20-00fa643012f1&lang=ru_RU'
            this.ymaps && this.ymaps.ready(() => {
                this.initMap();
            });
        }
    }

    initMap() {
        this.getCoords();
        this.createMap();
    }

    getCoords() {
        let coords = $(this.coordsElem).data('mapCoords');
        let center = $(this.coordsElem).data('mapCenter');
        this.mapCoords = coords;
        this.mapCenter = center;
    }

    createMap() {
        const mapBlock = document.querySelector('#map');
        this.myMap = new this.ymaps.Map(
            'map',
            {
                center: this.mapCenter,
                zoom: 15,
                controls: []
            },
        );

        this.mapCoords.forEach(item => {
            let name = item.id;
            let markLayout = ymaps.templateLayoutFactory.createClass(`<div class="map__mark map__mark--${name}" data-mark-coord=${item.pos}><svg width="39" height="52"><use xlink:href="#icon-${name}"></use></svg></div>`);

            let myPlacemark = new ymaps.Placemark(item.pos, {
                hintContent: ''
            }, {
                iconLayout: markLayout,
                iconShape: {
                    type: 'Rectangle',
                    coordinates: [[-30, -50], [30, 50]]
                }
            });

            this.myMap.geoObjects.add(myPlacemark);

            myPlacemark.events.add('mouseenter', () => {
                this.handlePlacemarkClass('map__mark--hover', 'add', name, mapBlock)
            });

            myPlacemark.events.add('mouseleave', () => {
                this.handlePlacemarkClass('map__mark--hover', 'remove', name, mapBlock)
            });

            myPlacemark.events.add('click', () => {
                this.handlePlacemarkClass('map__mark--active', 'toggle', name, mapBlock)
            });
        })
    }

    handlePlacemarkClass(className, classListAction, id, block) {
        const marks = block.querySelectorAll('.map__mark');
        marks.forEach(item => {
            if (item.classList.contains(`map__mark--${id}`)) {
                switch (classListAction) {
                    case 'add':
                        item.classList.add(className)
                        break
                    case 'remove':
                        item.classList.remove(className)
                        break
                    case 'toggle':
                        marks.forEach(item => { item.classList.remove(className) })
                        item.classList.toggle(className)
                        break
                }
            }
        })
    }
}